<template>
  <div>
    <el-form ref="form" :model="formData" label-width="80px">
      <el-form-item label="分类标题">
        <el-input v-model="formData.label"></el-input>
      </el-form-item>
      <el-form-item label="类型">
        <el-input v-model="formData.value"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="success" @click="submit" v-if="$route.name=='用户分类添加'">保存</el-button>
        <el-button type="success" @click="change"  v-if="$route.name=='用户分类编辑'">修改并保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>



  export default {
    name: "add",
    components: {
    },
    data() {
      return {
        formData: {
          label:'',
          value:''
        },
        token: '',
      }
    },
    methods: {
      submit(){
        this.$axios.post("adduserscate",this.formData).then((res)=>{
          if(res.code==200){
            this.$message({
              type: 'success',
              message: '提交成功!'
            });
          }else{
            this.$message({
              type: 'warning',
              message: res.msg
            });
          }
          setTimeout(()=>{
            this.$router.push("/crmusercategory")
          },1000)
        })
      },
      getdata(){
        this.$axios.post('getuserscate',{id: this.$route.query.id}).then(res=>{
          this.formData = res.data[0]
        })
      },
      change(){
        let params ={
          ...this.formData,
          id:this.$route.query.id
        }
        this.$axios.post("updateuserscate",params).then(res=>{
          if(res.code==200){
            this.$message({
              type: 'success',
              message: '修改成功!'
            });
            setTimeout(()=>{
              this.$router.push('/crmusercategory')
            },1000)
          }

        })
      }
    },
    created() {
      this.$nextTick(()=>{
        if(this.$route.name =="用户分类编辑"){
          this.getdata()
        }
      })

    },
    watch:{
      $route(to,from,next){
        this.formData = {
          title: '',
          author: '',
          img: '',
          content: "",
          contentText: ''
        }
      }
    }
  }
</script>
<style>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }

  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }

  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>
<style scoped>

</style>



